import { PropsWithChildren } from 'react'
import './message.css'
import { Modal } from 'antd'

type PropsModal = {
    isOpen:boolean,
    close : ()=>void
}


export const Message: React.FC<PropsWithChildren<PropsModal>> = (props: PropsWithChildren<PropsModal>) => {
    return (
        <div >
            <Modal
            className='modalBoby'
            open = {props.isOpen}
            onCancel={props.close}
            onOk={props.close}
            cancelText=" "
        
            >
                <p>
                        <br />
                      , <br />
                     :    <br />
                        <br />
                       <br />
                     , <br />
                       , <br />
                      <br />
                       , <br />
                      ,<br />
                      ,   <br />
                         <br />
                       , <br />
                      ,<br />
                         ,<br />
                        <br />
                       <br />
                      , <br />
                     : ,  <br />
                         <br />
                    , ,    , <br />
                      ,<br />
                       : <br />
                    !      <br />
                </p>
            </Modal>

        </div>
    )
}